export function block(options) {
  return `
      <div class="movie">
          <h2 class="movie__title h2"><a href="${options.url}" target="_blank">${options.title}</a></h2>
          <h3 class="movie__year h3">${options.year}</h3>
          <div class="rectangle"></div>
          <p class="movie__description h3">${options.description}</p>
          <div class="movie__filters">
            <button id="${options.id}" class="movie__status h3 ${!options.status ? 'unwatched' : 'watched'}">
              ${options.status ? '–ü—Ä–æ—Å–º–æ—Ç—Ä–µ–Ω–æ' : '–ù–µ –ø—Ä–æ—Å–º–æ—Ç—Ä–µ–Ω–æ'}
            </button>
            <p class="movie__genre h3">${options.genre}</p>
          </div>
          
      </div>
  `
}
export function generateFilter() {
  const filter = document.createElement('div')
  filter.classList.add('filter-block')
  filter.innerHTML = `
      <div class="filter-header" id="filter-header">
        <h3 class="filter-title h3">–§–∏–ª—å—Ç—Ä—ã</h3>
      </div>
      <form class="filter-form" id="filter-form">
        <div class="mb-3">
          <select class="form-select" name="genre">
            <option selected disabled>–ñ–∞–Ω—Ä</option>
            ${generateSelectGenre()}
          </select>   
        </div>
        <div class="mb-3">
          <label class="h3">–ì–æ–¥: </label>
          <input type="text" class="form-control" name="year">
        </div>
        <div class="mb-3">
          <label class="form-label h3">–ü—Ä–æ—Å–º–æ—Ç—Ä–µ–Ω–Ω–æ:</label>
          <input class="filter-checkbox" type="checkbox" name="status">  
        </div>
        <div>
          <input type="button" class="btn btn-secondary" id="clear" value="–û—á–∏—Å—Ç–∏—Ç—å">
          <input type="submit" class="btn btn-primary" value="–ü–æ–∏—Å–∫">
        </div>
      </form>
  `
  return filter
}

function generateSelectGenre() {
  const genre = ['–ö–æ–º–µ–¥–∏—è', '–ú—É–ª—å—Ç—Ñ–∏–ª—å–º', '–£–∂–∞—Å—ã', '–§–∞–Ω—Ç–∞—Å—Ç–∏–∫–∞', '–¢—Ä–∏–ª–ª–µ—Ä', '–ë–æ–µ–≤–∏–∫', '–ú–µ–ª–æ–¥—Ä–∞–º–∞', '–î–µ—Ç–µ–∫—Ç–∏–≤', '–ü—Ä–∏–∫–ª—é—á–µ–Ω–∏—è', '–ê–Ω–∏–º–µ', '–í–æ–µ–Ω–Ω–æ–µ']
  const toString = key => `<option value="${key}">${key}</option>`
  return genre.map(toString).join('')
}
export function generateForm() {
  return `
      <form class="movie-form" id="add-movie">
          <div class="mb-3">
            <label for="exampleFormControlInput1" class="form-label">–ù–∞–∑–≤–∞–Ω–∏–µ —Ñ–∏–ª—å–º–∞</label>
            <input type="text" name="title" class="form-control" id="exampleFormControlInput1">
          </div>
          <div class="mb-3">
            <label for="exampleFormControlInput2" class="form-label">–ì–æ–¥</label>
            <input type="number" name="year" min="1900" max="${new Date().getFullYear()}" value="2000" class="form-control" id="exampleFormControlInput2">
          </div>
          <div class="mb-3">
            <label for="exampleFormControlInput3" class="form-label">–°—Å—ã–ª–∫–∞ –Ω–∞ —Ñ–∏–ª—å–º</label>
            <input type="text" name="url" class="form-control" id="exampleFormControlInput3">
          </div>
          <div class="mb-3">
            <label for="exampleFormControlTextarea1" class="form-label">–û–ø–∏—Å–∞–Ω–∏–µ —Ñ–∏–ª—å–º–∞</label>
            <textarea class="form-control" name="description" id="exampleFormControlTextarea1" rows="3" maxlength="700"></textarea>
          </div>
          <div class="mb-3">
            <select class="form-select" name="genre">
              <option selected disabled>–í—ã–±–µ—Ä–∏—Ç–µ –∂–∞–Ω—Ä</option>
              ${generateSelectGenre()}
            </select>
          </div>
          <div class="modal-footer">
            <input type="submit" class="btn btn-primary" value="–î–æ–±–∞–≤–∏—Ç—å" data-bs-dismiss="modal">
          </div>
      </form>
  `
}


export function generateModal(content) {
  const modal = document.createElement('div')
  modal.classList.add('modal')
  modal.setAttribute('tabindex', -1)
  modal.setAttribute('id', 'open-modal')
  modal.innerHTML = `
      <div class="modal-dialog modal-dialog-centered modal-xl">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">–î–æ–±–∞–≤–∏—Ç—å —Ñ–∏–ª—å–º</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body">
            ${content}
          </div>
        </div>
      </div>
  `
  return modal
}

export function fromObjectToAttribute(target, obj = {}) {
  for (let key in obj){
    target.setAttribute(key, obj[key])
  }
  return target
}